.step {
	position: relative;
	width: 900px;
	padding: 10px;
	margin: 20px auto;
	-ms-box-sizing: border-box;
	-o-box-sizing: border-box;
	box-sizing: border-box;
	font-family: Roboto, San Francisco, \\5FAE\8EDF\6B63\9ED1\9AD4, "sans-serif";
	font-size: 48px;
	line-height: 1.5;
	color: white
}

.step .h1,
.step h1 {
	font-size: 124.8px;
}

.step .h2,
.step h2 {
	font-size: 103.2px
}

.step .h3,
.step h3 {
	font-size: 81.6px
}

.step .h4,
.step h4 {
	font-size: 62.4px
}

.step .h5,
.step h5 {
	font-size: 48px
}

.step .h6,
.step h6 {
	font-size: 40.8px
}

.impress-enabled .step {
	margin: 0;
	opacity: .3;
	-webkit-transition: opacity 1s;
	transition: opacity 1s
}

.impress-enabled .step.active {
	opacity: 1;
	cursor: default
}

.impress-enabled .step a {
	color: hsla(0, 0%, 100%, .8);
	text-decoration: none;
	padding: 0 .4em;
	background: #3e4852;
	border-radius: .2em;
	-webkit-transition: all .5s;
	transition: all .5s
}

.impress-enabled .step a:focus,
.impress-enabled .step a:hover {
	color: #e5b560;
	background: #495561;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, .2), 0 6px 20px 0 rgba(0, 0, 0, .19);
	text-decoration: none
}

.impress-enabled .step b {
	display: inline-block;
	-webkit-transition: all .5s;
	transition: all .5s
}

.impress-enabled .step.present .positioning {
	-webkit-transform: translateY(-10px);
	transform: translateY(-10px)
}

.impress-enabled .step.present .positioning,
.impress-enabled .step.present .positioning-down {
	-webkit-transition-delay: 1.25s;
	transition-delay: 1.25s
}

.impress-enabled .step.present .positioning-down {
	-webkit-transform: translateY(10px);
	transform: translateY(10px)
}

.impress-enabled .step.present .rotating {
	-webkit-transform: rotate(-10deg);
	transform: rotate(-10deg)
}

.impress-enabled .step.present .rotating,
.impress-enabled .step.present .rotating-right {
	-webkit-transition-delay: 1.5s;
	transition-delay: 1.5s
}

.impress-enabled .step.present .rotating-right {
	-webkit-transform: rotate(10deg);
	transform: rotate(10deg)
}

.impress-enabled .step.present .scaling {
	-webkit-transform: scale(.7);
	transform: scale(.7)
}

.impress-enabled .step.present .scaling,
.impress-enabled .step.present .scaling-up {
	-webkit-transition-delay: 1.75s;
	transition-delay: 1.75s
}

.impress-enabled .step.present .scaling-up {
	-webkit-transform: scale(1.3);
	transform: scale(1.3)
}

.slide {
	display: block;
	line-height: 36px;
	width: 900px;
	height: 700px;
	padding: 40px 60px;
	background-color: #fff;
	border: 1px solid rgba(0, 0, 0, .3);
	border-radius: 3px;
	box-shadow: 0 8px 16px rgba(0, 20, 80, .04), 0 4px 16px rgba(0, 0, 0, .08);
	color: #666;
	text-shadow: 0 2px 2px rgba(0, 0, 0, .1);
	font-size: 35px;
	letter-spacing: 1px
}

.slide .h1,
.slide h1 {
	font-size: 91px
}

.slide .h2,
.slide h2 {
	font-size: 75.25px
}

.slide .h3,
.slide h3 {
	font-size: 59.5px
}

.slide .h4,
.slide h4 {
