}

.slide:hover {
	box-shadow: 0 8px 16px rgba(0, 40, 80, .08), 0 8px 24px 5px rgba(0, 0, 0, .08)
}

#overview {
	display: none
}

.impress-on-overview .step {
	opacity: 1
}

.hint {
	opacity: 0;
	-webkit-animation: fadeOutUp .6s ease forwards;
	        animation: fadeOutUp .6s ease forwards;
	color: hsla(0, 0%, 100%, .8);
	background-color: #3e4852;
	font-size: 20px;
	text-align: center;
	width: 400px;
	min-height: 100px;
	line-height: 100px;
	position: fixed;
	left: 25px;
	bottom: 14%;
	z-index: 9999;
	border-radius: 3px;
	-webkit-filter: drop-shadow(0 8px 8px rgba(0, 0, 0, .3));
	        filter: drop-shadow(0 8px 8px rgba(0, 0, 0, .3));
	display: none;
	visibility: hidden;
}

.hint b {
	color: #e5b560
}

.mobile-hint {
	opacity: 0;
	-webkit-animation: fadeOutUp .6s ease forwards;
	        animation: fadeOutUp .6s ease forwards;
	text-align: center;
	color: #3e4852;
	width: 128px;
	position: fixed;
	left: calc(50% - 64px);
	bottom: 10%
}

.mobile-hint img {
	display: block;
	margin: 0 auto;
	width: 48px
}

.mobile-hint span {
	display: block;
	margin-top: 5px
}

.show .hint,
.show .mobile-hint {
	-webkit-animation: fadeInDown .6s ease 1s forwards;
	        animation: fadeInDown .6s ease 1s forwards
}

.impress-enabled .hint,
.impress-enabled .mobile-hint {
	display: block
}

@-webkit-keyframes fadeInDown {
	0% {
		opacity: 0;
		-webkit-transform: translate3d(0, -100%, 0);
		        transform: translate3d(0, -100%, 0)
	}
	to {
		opacity: 1;
		-webkit-transform: none;
		        transform: none
	}
}

@keyframes fadeInDown {
	0% {
		opacity: 0;
		-webkit-transform: translate3d(0, -100%, 0);
		        transform: translate3d(0, -100%, 0)
	}
	to {
		opacity: 1;
		-webkit-transform: none;
		        transform: none
	}
}

@-webkit-keyframes fadeOutUp {
	0% {
		opacity: 1
	}
	to {
		opacity: 0;
		-webkit-transform: translate3d(0, 100%, 0);
		        transform: translate3d(0, 100%, 0)
	}
}

@keyframes fadeOutUp {
	0% {
		opacity: 1
	}
	to {
		opacity: 0;
		-webkit-transform: translate3d(0, 100%, 0);
		        transform: translate3d(0, 100%, 0)
	}
}

.slide q {
